import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MenuController, ToastController } from '@ionic/angular';
import { ApiService } from 'src/app/Services/Api/Api.service';

@Component({
  selector: 'app-diamante-mtto',
  templateUrl: './diamante-mtto.page.html',
  styleUrls: ['./diamante-mtto.page.scss'],
})
export class DiamanteMttoPage implements OnInit {

  list = [];
  locs = [];

  desc = '';
  centro = '';

  selectDesc;
  selectCentro;

  iniciacion;
  tipo;
  load;

  tkn;
  

  constructor(
    private api: ApiService,
    private toas: ToastController,
    private route: ActivatedRoute,
    private menuCtrl: MenuController
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {

    this.menuCtrl.enable(false, 'menu')
    this.route.queryParams.subscribe((param) => {
      console.log(param);
      this.tkn = param.accesstoken;
      this.gestared()
    })

  

    
  }

  gestared() {

    this.api.getLists({
      AccessToken: this.tkn,
      ListTypeGUID: "AV0lV3ARVx"
    }).subscribe((data : any) => {
      this.list = data;
      console.log(data)
    }) 

    this.api.getLocs({
      AccessToken: this.tkn,
      LocationTypeID: "73E2C37A-5570-4134-BDCA-5C394167F877"
    }).subscribe((data : any) => {
      this.locs = data;
      console.log(data)
    }) 

  }

  asignar(event) {
    this.tipo = event.detail.value;
  }

  cambio(event, type) {
    if (type == 1) {
      this.desc = event.detail.value;
    }

    if (type == 2) {
      this.centro = event.detail.value;
    }
  }

  select(item, type) {

    if (type == 1) {
      this.selectDesc = item;
      this.desc = '';
    }

    if (type == 2) {
      this.selectCentro = item;
      this.centro = '';
    }

   
  }

  async msg(txt) {
    const toast = await this.toas.create({
      message: txt,
      duration: 2000
    })

    await toast.present();
  }

  createAsset() {
    if (!this.iniciacion) {
      this.msg('Debe dìgitar el No. de iniciaciòn')
      return;
    }
    if (!this.tipo) {
      this.msg('Debe seleccionar un tipo de asignaciòn')
      return;
    }

    if (!this.selectDesc) {
      this.msg('Debe seleccionar un equipo')
      return;
    }

    if (!this.selectCentro) {
      this.msg('Debe seleccionar un centro de costos')
      return;
    }

    this.load = true;

    this.api.createAsset({
      tkn: this.tkn,
      Name: this.selectDesc.Name,
      AssetTypeGUID: '1JJkNDXMay',
      LocationGUID: this.selectCentro.GUID,
      Values: JSON.stringify([
        {
          apiId: 'REFERENCIA',
          Value: this.selectDesc.Values[0].REFERENCIA
        },
        {
          apiId: 'INICIACION',
          Value: this.iniciacion
        },
        {
          apiId: 'ASIGNACION',
          Value: this.tipo
        },
        {
          apiId: 'VIDAUTIL',
          Value: this.selectDesc.Values[2].VIDAUTIL
        }
      ])
    }).subscribe((data : any) => {
  
      console.log(data, 'resp')

      if (data.Status == 'OK') {
        this.msg('Activo creado correctamente')
        this.iniciacion = '';
        this.selectCentro = false;
        this.selectCentro = false;

      } else {
        this.msg('Error, No se pudo crear el activo')
      }

      this.load = false;
      
    }) 
  }

}
